import './menuRender.styl'

export const menuRender = () => {
  let menu = $(
    `<div class='modal-wrap' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'>
    <div class='menu_container'>
    <div class='menu-wrapper'>
      <div class='list-wrap'>
      <div class='menu-logo'>
        <div class='logo'>
          <img class='business_logo' src=${config.user.business_logo} />
        </div>
        <p class='business_name' >${config.user.business_name}</p>
        <p class='business_address'><img class='business_address_img' src='${`${config.urls.menu_icons}map-pin.svg`}'>${config.user.business_address}</p>
      </div>
      <nav class='menu-list'>
        ${config.menu.reduce((phrase, item) => {
    return phrase + `<li class='list-item'>
            <a class='item-link' href='${item.link}'>
              <img class='menu-img' src='${config.urls.menu_icons + item.icon}' alt='${item.text}' />
              ${config.translations.menu[item.text]}
            </a>
          </li>`
  }, '')}
    </nav>
    </div>
    </div>
    </div>
  </div>`
  )
  $('body').append(menu)
  $('.menu-wrapper').css({'backgroundImage': 'url(' + config.urls.menu_icons + 'photo-bgr.png'})
  let menuBtn = $('.more_wrap')[0]
  menuBtn.addEventListener('click', () => {
    $('.modal-wrap').toggle()
    if ($('.modal-wrap').css('display') === 'block') {
      $($('html')[0]).addClass('no-scroll')
      $($('body')[0]).addClass('no-scroll')
    } else {
      $($('html')[0]).removeClass('no-scroll')
      $($('body')[0]).removeClass('no-scroll')
    }
  })
  let modalWrap = $('.modal-wrap')[0]
  let menuWrapper = $('.menu-wrapper')[0]
  menuWrapper.addEventListener('click', e => e.stopPropagation())
  modalWrap.addEventListener('click', () => {
    $('.modal-wrap').css({'display': 'none'})
    $($('html')[0]).removeClass('no-scroll')
    $($('body')[0]).removeClass('no-scroll')
  })
}
