import './floatingButton.styl'
import qs from 'qs'
export const floatingButton = () => {
  let button = $(
    `
    <div class='floating-button ${config.calendar.isRTL === false ? 'standartRight' : 'standartLeft'}'><img class='cross' src='${config.urls.staticImg}/plus.svg' /></div>
    `
    )
    $('#calendar').after(button)
    $(button).on('click', () => {
      let obj = qs.parse(window.location.search.slice(1))
      let view = $('#calendar').fullCalendar('getView')
      let start = $('#calendar').fullCalendar('getDate').format('YYYY-MM-DD')
      if (view.name === 'daily' || view.name === 'agendaDay') {
        window.location = `${config.urls.creatingAppointmentLink}?worker_id=${config.activeWorkerId}&start=${start}&calendar_view_type=${obj.calendar_view_type}`
      } else {
        window.location = `${config.urls.creatingAppointmentLink}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`
      }
  })
}
