import { customEventFull } from '../app/components/daily/daily-events/eventFull.js'
import { customEventHalf } from '../app/components/daily/daily-events/eventHalf.js'
import { customEventThird } from '../app/components/daily/daily-events/eventThird.js'
import { customEventExtra } from '../app/components/daily/daily-events/eventExtra.js'
import { customEventWeeklyFull } from '../app/components/weekly/weekly-events/eventFull.js'
import { customEventWeeklyOverley } from '../app/components/weekly/weekly-events/eventOverlay.js'
export const getHoursLabel = (hours, minutes) => {
  let newHours,
    newMinutes

  hours && hours.length === 1
    ? newHours = '0' + hours
    : newHours = hours
  if (minutes && minutes.length === 1) {
    minutes === 0
      ? newMinutes = minutes + '0'
      : newMinutes = '0' + minutes
  } else { newMinutes = minutes }

  return newHours + ':' + newMinutes
}

export const timeToText = seconds => {
  let hours = Math.floor(seconds / 3600000)
  seconds -= hours * 3600000
  let minutes = Math.floor(seconds / 60000)

  if (hours < 10) {
    hours = '0' + hours
  }
  if (minutes < 10) {
    minutes = '0' + minutes
  }
  return hours + ':' + minutes
}

export const customTimeToText = seconds => {
  let hours = Math.floor(seconds / 3600000)
  seconds -= hours * 3600000
  let minutes = Math.floor(seconds / 60000)
  if (hours === 1 && minutes === 0) {
    return `${hours} ${config.translations.duration.hour}`
  }
  if (hours === 0) {
    return `${minutes} ${config.translations.duration.minutes}`
  } else if (minutes === 0) {
    return `${hours} ${config.translations.duration.hours}`
  } else {
    return `<p class='full-dur'><span class='hours'>${hours}</span><span>${config.translations.duration.abbreviated_hours}</span><span class='minutes'>${minutes}</span><span>${config.translations.duration.abbreviated_minutes}</span></p>`
  }
}

export const textToTime = str => {
  return str.slice(0, 2) * 3600 + str.slice(3, 5) * 60
}

export const arraySum = arr => {
  let sum = 0
  for (let i = 0; i < arr.length; i++) {
    sum += arr[i]
  }
  return sum
}

export const getDayColor = (sum, totalSum) => {
  const coef = sum / totalSum
  if (coef === 0) { return 'dayOfRest' }
  if (coef < 0.3) { return 'easyDay' }
  if (coef < 0.6) { return 'normalDay' }
  if (coef <= 1 || coef > 1) { return 'busyDay' }
}

export const bday = (event, date) => {
  if (event.birthdate.length === 5) {
    if (moment(`2018-${event.birthdate}`).format('MM') === moment(date).format('MM')) {
      return `<span class='bday'><img class='bday-img' src='${config.urls.staticImg}/bday.svg'></span>`
    } else {
      return ''
    }
  } else if (event.birthdate.length === 10) {
    if (moment(event.birthdate).format('MM') === moment(date).format('MM')) {
      return `<span class='bday'><img class='bday-img' src='${config.urls.staticImg}/bday.svg'></span>`
    } else {
      return ''
    }
  }
}

const colorStr = services => {
  let nextNum = 0
  let colorStr = ''
  if (services) {
    const oneColorHeight = (100 / services.length).toFixed(2)
    for (let i = 0, l = services.length; i < l; i++) {
      const newNextNum = +nextNum + +oneColorHeight
      colorStr += ', ' + services[i].color + ' ' + nextNum + '%, ' + services[i].color + ' ' + newNextNum + '%'
      nextNum = newNextNum
    }
  }
  return colorStr
}
const durationInMin = (start, end) => {
  return Math.floor((end - start) / 60000)
}
export const rowCount = (start, end) => {
  let duration = Math.floor((end - start) / 60000)
  let arr = config.calendar.slotDuration.split(':')
  let ss = +arr[2] * 1000
  let mm = +arr[1] * 60000
  let hh = +arr[0] * 3600000
  let slotDuration = (ss + mm + hh) / 60000
  return duration / slotDuration
}

const draggingResizing = (event, element, start, end, view) => {
  let container = $(element[0].children[0])
  $(container).addClass('ev-custom')
  let customEvent = $(
    `<div class='info-time'>
      <p class='event-start'>${start} - ${end}</p>
      <div class='short-cont'>
              <div class='duration-wrap'>
                <img class='mini' src='${config.urls.staticImg}/clock.svg'>
                <div class='event-duration'>${customTimeToText(event.end - event.start)}</div>
              </div>
            </div>
    </div>`
  )
  $(customEvent).appendTo(container)
}
const toHexInt = i => parseInt(i, 16)

const _mixColors = (color1, color2) => {
  let color = ''
  for (let i = 0; i < color1.length; i += 2) {
    let partColor = Math.round((toHexInt(color1.slice(i, i + 2)) + toHexInt(color2.slice(i, i + 2))) / 2).toString(16)

    color += (partColor.length === 1 ? '' + partColor : partColor)
  }
  return color
}
const mixColors = (color1, color2) => {
  var c1 = color1[0] === '#' ? color1.slice(1) : color1
  var c2 = color2[0] === '#' ? color2.slice(1) : color2
  return '#' + _mixColors(c1, c2)
}
export const eventRender = (event, element, view) => {
  element.attr('data-appointment_id', event.id)
  if (view.name === 'daily' || view.name === 'agendaFourDay') {
    $(element[0]).css(config.calendar.isRTL ? {'direction': 'rtl'} : {'direction': 'ltr'})
    let color = event.services && event.services.length > 0 && event.services[0].color
      ? event.services[0].color
      : event.off_time
        ? '#777777'
        : '#4f2da7'
    $(element[0]).css({'border-image': 'linear-gradient(to bottom' + colorStr(event.services) + ') 1 100%', 'backgroundColor': mixColors(color, '#FFFFFF')})
    let start = getHoursLabel(event.start.hours().toString(), event.start.minutes().toString())
    let end = event.end
      ? getHoursLabel(event.end.hours().toString(), event.end.minutes().toString())
      : ''
    draggingResizing(event, element, start, end, view)
  }
  if (view.name === 'month') {
    let services = event.services
      ? event.services.reduce((phrase, item) => {
        return item.name && !!item.name ? phrase + `${item.name}` + (item.count > 1 ? ` ⨯ ${item.count},` : '') : phrase
      }, '')
      : []
    if (event.services) {
      // services = services.slice(0, -1)
      services = services.split(',')
    }
    let monthEvent = $(element[0].children[0])[0]
    let color = event.services && event.services[0] && event.services[0].color ? event.services[0].color : 'transparent'
    let title = event.off_time
      ? `<p class='month_title off_time_service' > ${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>`
      : event.services && !!event.services ? `<p class='month_title' ><span class='square'>&bull;</span><span class='service-item'>${event.name ? event.name : config.translations.occasional}</span></p>` : ''
    $(monthEvent).addClass(config.calendar.isRTL ? 'title_rtl' : '')
    $(monthEvent).attr('data-event', event.start.format('YYYY-MM-DD'))
    if (moment($(monthEvent).attr('data-event')).format('MM') !== $('#calendar').fullCalendar('getDate').format('MM')) {
      $(monthEvent).addClass('not-current-month')
    }
    $(monthEvent).css({'borderColor': color})
    if ($(title).hasClass('off_time_service')) {
      $(monthEvent).addClass('off_time_title')
    }
    $(monthEvent).html(title)
  }
}

const setCellInvisability = () => {
  const allMoreBlock = $('.fc-more-cell')
  for (let i = 0, n = allMoreBlock.length; i < n; i++) {
    const target = getPosition(allMoreBlock[i])
    const otherMont = $(document.elementsFromPoint(target.x, target.y)).filter('.fc-bg .fc-other-month')
    if (otherMont.length) {
      $(allMoreBlock[i]).find('.fc-more').css({'filter': 'opacity(55%)'})
    }
  }
}

const getPosition = el => {
  let lx, ly
  for (lx = 0, ly = 0; el != null; lx += el.offsetLeft, ly += el.offsetTop, el = el.offsetParent);
  return {x: lx, y: ly}
}

export const eventAfterRender = (event, element, view) => {
  if (view.name === 'daily' || view.name === 'agendaFourDay') {
    $(element[0].children[0]).empty()
    let start = getHoursLabel(event.start.hours().toString(), event.start.minutes().toString())
    let end = event.end
      ? getHoursLabel(event.end.hours().toString(), event.end.minutes().toString())
      : ''
    const elemWidth = $(element).width()
    if (view.name === 'daily') {
      if (elemWidth <= 245.5 && elemWidth > 122.4) {
        customEventHalf(event, element, start, end, view)
      } else if (elemWidth <= 91 && elemWidth < 122.4) {
        customEventExtra(event, element, start, end, view)
      } else if (elemWidth <= 122.4) {
        customEventThird(event, element, start, end, view)
      } else {
        customEventFull(event, element, start, end, view)
      }
    }
    if (view.name === 'agendaFourDay') {
      if (elemWidth <= 60) {
        customEventWeeklyOverley(event, element, start, end, view)
      } else {
        customEventWeeklyFull(event, element, start, end, view)
      }
    }
  }
}

const bgrColor = (date, element) => {
  const items = window.loadedEventsMap[date.format('YYYY-MM-DD')]
  $(element).removeClass('dayOfRest easyDay normalDay busyDay')
  const businessHours = $('#calendar').fullCalendar('option', 'businessHours')
  if (items && businessHours) {
    const times = items.map(i => moment(i.end) - moment(i.start))
    const filteredBusinessHours = businessHours.filter(item => item.dow.includes(moment(date).day()))
    const totalHour = filteredBusinessHours.length !== 0 && (textToTime(filteredBusinessHours[0].end) - textToTime(filteredBusinessHours[0].start)) * 1000
    $(element).addClass(filteredBusinessHours.length === 0
      ? 'dayOff'
      : getDayColor(arraySum(times), totalHour))
  }
}

export const eventAfterAllRender = view => {
  if (view.name === 'month') {
    setTimeout(() => {
      if (window.loadedEvents) {
        let day = $('.fc-day-grid .fc-bg .fc-day')
        for (let index = 0; index < day.length; index++) {
          const element = day[index]
          const date = moment($(element).attr('data-date'))
          bgrColor(date, element)
        }
      }
    }, 10)
  }
  setCellInvisability()
}
