export const timePanel = () => {
  let view = $('#calendar').fullCalendar('getView')
  if (view.name === 'daily' && $('.timeBox').length === 0) {
    let container = $('.fc-daily-view .fc-body')
    let timeBox = $('<div>', {class: 'timeBox'}).appendTo(container)
    let boxWidth = $('.fc-bg .fc-axis.fc-widget-content').innerWidth()
    let boxHeight = $('.fc-daily-view').height()
    let timeWeekHeight = $('.fc-head .fc-day-header').innerHeight()
    $(timeBox).css({'width': boxWidth + 1, 'height': boxHeight})
    let arrSpan = $('.fc-axis.fc-time span')
    let div = arrSpan.map((index, item) => {
      return `<div class='time-week'><span class='time-text'>${$(item).text()}</span></div>`
    })
    let timeHtml = [...div].join('')
    $(timeHtml).appendTo($('.timeBox'))
    setTimePanelTime()
  }
  if (view.name === 'agendaFourDay' && $('.timeBox').length === 0) {
    let timeBox = $('<div>', {class: 'timeBox'}).appendTo($('.fc-agendaFourDay-view'))
    $('<div>', {class: 'header-week'}).appendTo($('.timeBox'))
    let boxWidth = $('.fc-bg .fc-axis.fc-widget-content').innerWidth()
    let boxHeight = $('.fc-agendaFourDay-view').height()
    let timeWeekHeight = $('.fc-head .fc-day-header').innerHeight()
    $(timeBox).css({'width': boxWidth + 1, 'height': boxHeight})
    $('.header-week').css({'height': timeWeekHeight + 2})
    let arrSpan = $('.fc-axis.fc-time span')
    let div = arrSpan.map((index, item) => {
      return `<div class='time-week'><span class='time-text'>${$(item).text()}</span></div>`
    })
    let timeHtml = [...div].join('')
    $(timeHtml).appendTo($('.timeBox'))
    setTimePanelTime()
  }
  $('.timeBox').css(config.calendar.isRTL ? {'right': 0, 'borderLeft': '1px solid #ededed'} : {'left': 0})
}

const setTimePanelTime = () => {
  let view = $('#calendar').fullCalendar('getView')
  if (view.name === 'daily') {
    let timeBlock = $('.time-text').slice(1)
    timeBlock.map((index, element) => {
      if ($(element).text().slice(3) === '00') {
        let a = $(element).html().slice(0, 2)
        $($(element).parent()[0]).addClass('true-time-daily')
        $(element).addClass('margin-time')
        return $(element).html(a)
      } else if ($(element).text().length > 2) {
        $(element).text('')
      }
    })
    let firstSlot = $('.time-text')[0]
    $(firstSlot).addClass('margin-time')
    $(firstSlot).html($(firstSlot).text().slice(0, 2))
  }
  if (view.name === 'agendaFourDay') {
    let timeBlock = $('.time-text').slice(1)
    timeBlock.map((index, element) => {
      if ($(element).text().slice(3) === '00') {
        let a = $(element).html().slice(0, 2)
        $($(element).parent().prev()[0]).addClass('true-time')
        $(element).addClass('margin-time')
        return $(element).html(a)
      } else if ($(element).text().length > 2) {
        $(element).text('')
      }
    })
    let firstSlot = $('.time-text')[0]
    $(firstSlot).addClass('margin-time')
    $(firstSlot).html($(firstSlot).text().slice(0, 2))
  }
}
