import './home.styl'
import { initEvents, initSwiper, preloader, initEditionalFeatures } from '../baseEvents'
import { menuRender } from '../menu/menuRender'
import { installModal } from '../installModal/installModal'
import { initHeader } from '../header/header'
import { floatingButton } from '../floatingButton/floatingButton'
import qs from 'qs'
import {
  handleViewRender,
  columnHeaderText,
  eventAfterRender,
  eventAfterAllRender,
  handleDayClick,
  eventResize,
  eventRender,
  eventDrop
} from 'helpers'

$(document).ready(() => {
  let body = document.getElementsByTagName('body')[0]
  if (config.calendar.isRTL) body.style.direction = 'rtl'

  initHeader()
  let activeWorker = config.workers.find(worker => worker.id == config.activeWorkerId)
  // let obj = qs.parse(window.location.search.slice(1))
  // let defaultView = obj.calendar_view_type ? config.match_view[obj.calendar_view_type] : config.match_view[config.calendar_view_type]
  let businessHours = activeWorker.businessHours
  $('#calendar').fullCalendar({
    ...config.calendar,
    businessHours,
    // defaultView,
    fixedWeekCount: false,
    viewRender: handleViewRender,
    dayClick: handleDayClick,
    columnHeaderText,
    eventRender,
    eventAfterRender,
    eventAfterAllRender,
    eventResize,
    eventDrop
  })
  initSwiper()
  preloader()
  initEditionalFeatures()
  initEvents()
  floatingButton()
  menuRender()
  installModal()
  let view = $('#calendar').fullCalendar('getView')
  window.addEventListener('popstate', () => {
    if ($('.eventPopupCont').length > 0) {
      // let obj = qs.parse(window.location.search.slice(1))
      // history.replaceState(null, null, `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`)
      $('.eventPopupCont').remove()
      $($('html')[0]).removeClass('no-scroll')
      $($('body')[0]).removeClass('no-scroll')
      const date = $('#calendar').fullCalendar('getDate')
      $('#calendar').fullCalendar('gotoDate', date)
    } else {
      $('#calendar').fullCalendar('option', 'contentHeight', 'auto')
      // let obj = qs.parse(window.location.search.slice(1))
      const date = $('#calendar').fullCalendar('getDate')
      $('#calendar').fullCalendar('changeView', config.match_view[config.calendar_view_type], date)
      if (view.name !== 'agendaFourDay') {
        $('.custom-calendar-header').addClass('hidden')
      }
      initEditionalFeatures()
    }
  })

  // let search = location.search && location.search.includes('calendar_view_type=') ? location.search : location.search + `&calendar_view_type=${config.calendar_view_type}`
  // history.pushState(null, null, search
  //   ? `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}` + search
  //   : `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}?worker_id=${config.activeWorkerId}&calendar_view_type=${config.calendar_view_type}`)
})
