import {rowCount, bday, customTimeToText} from 'helpers'

export const customEventFull = (event, element, start, end, view) => {
  let container = $(element[0].children[0])
  $(container).addClass('ev-custom')
  let services = event.services
    ? event.services.reduce((phrase, item) => {
      return item.name && !!item.name ? phrase + `${item.name}` + (item.count > 1 ? ` ⨯ ${item.count},` : '') : phrase
    }, '')
    : []
  if (event.services) {
    // services = services.slice(0, -1)
    services = services.split(',')
  }
  let customEvent
  if (rowCount(event.start, event.end) >= 0.5 && rowCount(event.start, event.end) <= 1) {
    customEvent = $(
      event.off_time
        ? `<div class='click-mask'></div>
        <div class='off_time short'>
          <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
          <p class='event-start'>${start} - ${end}</p>
        </div>`
        : `<div class='click-mask'></div>
        <div class='custom-event'>
    <div class='name-service flex'>
            ${event.name
    ? `<p class='client-name ${rowCount(event.start, event.end) === 1 ? 'oneRow' : ''}'>${event.name}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`
    : `<p class='client-name ${rowCount(event.start, event.end) === 1 ? 'oneRow' : ''}'>${config.translations.occasional}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`}
    <p class='services-name single'>${services.length > 1 ? services[0] + ' +' + (services.length - 1) : services[0]}</p>
      ${event.is_new_client || event.durationEditable ? `<div class='iconsEditional'>
      ${event.durationEditable ? `<span class='paid'>${config.translations.paid}</span>` : event.is_new_client ? `<img class='new-icon' src='${config.urls.staticImg}/new.svg'>` : ''}
          </div>` : ''}
      <div class='time-info single-time'>
        <p class='event-start'>${start} - ${end}</p>
      </div>
    </div>`
    )
  }
  if (rowCount(event.start, event.end) > 1) {
    customEvent = $(
      event.off_time
      ? `<div class='click-mask'></div>
          <div class='off_time'>
            <div class='off_time_extended'>
              <div class='off_time_wrap'>
                <img class='off_time_img' src='${event.off_time === 'break' ? `${config.urls.staticImg}/break.svg` : `${config.urls.staticImg}/meeting.svg`}' />
              </div>
              <div class='main_info'>
              <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
              ${event.address
    ? `<div class='address-wrap'>
                  <div class='address-img-wrap'>
                    <img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'>
                  </div>
                  <p class='client-address'>${event.address}</p>
                </div>`
    : ''}
                ${event.note && (rowCount(event.start, event.end) > 2 || !event.address)
    ? `<div class='note-wrap'>
                    <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
                    <p class='client-note'>${event.note}</p>
                  </div>`
    : ''}
                </div>
            </div>
            <div class='off_time-info'>
              <p class='event-start'>${start} - ${end}</p>
              <div class='short-cont'>
                <div class='duration-wrap'>
                  <img class='mini' src='${config.urls.staticImg}/clock.svg'>
                  <div class='event-duration'>${customTimeToText(event.end - event.start)}</div>
                </div>
              </div>
            </div>
          </div>`
        : `<div class='click-mask'></div>
        <div class='custom-event'>
        <div class='main-info'>
          <div class='foto-time'>
             <div class='foto-name'>
            ${view.name === 'daily'
    ? `${event.profile_picture && !!event.client_id && event.client_id >= 0
      ? `<img class='fit normalSize' src='${config.urls.imgForClients.replace('{client_id}', `${event.client_id}`) + event.profile_picture}' onerror="this.onerror=null;this.src='${config.urls.defaultPathToClientImg}${config.defaultClientPhoto}';"/>` : !event.client_id || event.client_id <= 0 ?
        `<img class='normalSize fit' src='${config.urls.imgForOccasionalClients}${config.occasionalClientPhoto}' />` :
        `<img class='normalSize fit' src='${config.urls.defaultPathToClientImg}${config.defaultClientPhoto}' />`
    }
    `
    : ''
}
            <div class='name-service'>
            ${event.name
    ? `<p class='client-name'>${event.name}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`
    : `<p class='client-name'>${config.translations.occasional}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`}
            <p class='services-name'>${services.map(i => `<span class='service-item'>${i}</span>`).join(' ')}</p>
            </div>
          </div>
          ${event.is_new_client || event.durationEditable ? `<div class='iconsEditional'>
            ${event.durationEditable ? `<span class='paid'>${config.translations.paid}</span>` : ''}
            ${event.is_new_client ? `<img class='new-icon' src='${config.urls.staticImg}/new.svg'>` : ''}
          </div>` : ''}
          <div class='time-info'>
            <p class='event-start'>${start} - ${end}</p>
            <div class='short-cont'>
              <div class='duration-wrap'>
                <img class='mini' src='${config.urls.staticImg}/clock.svg'>
                <div class='event-duration'>${customTimeToText(event.end - event.start)}</div>
              </div>
            </div>
          </div>
        </div>
        ${rowCount(event.start, event.end) > 2 ? `<div class='note-address'>
        ${event.address && rowCount(event.start, event.end) > 2
    ? `<div class='address-wrap'>
        <div class='address-img-wrap'>
            <img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'>
        </div>
    <p class='client-address'>${event.address}</p>
    </div>`
    : ''}
          ${event.note && (rowCount(event.start, event.end) > 3 || !event.address)
    ? `<div class='note-wrap'>
      <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
      <p class='${`client-note ${rowCount(event.start, event.end) <= 6 ? 'nowrap' : ''}`}'>${event.note}</p>
    </div>`
    : ''}
        </div>` : ''}
      </div>
    `)
  }
  $(customEvent).appendTo(container)
}
