import {changeView, checkShowEvent, showPopupFromUrl} from 'helpers'
import {fetchEvenes, holidayData} from '../baseEvents'
import './daily.styl'
const dailyHoliday = () => {
  if (localStorage.getItem('holidays_data') && !$('.holiday').length) {
    try {
      let holidaysData = JSON.parse(localStorage.getItem('holidays_data'))
      let data = holidaysData.data
      let calendarDate = $('#calendar').fullCalendar('getDate').format('YYYY-MM-DD')
      let holiday
      for (let key in data) {
        holiday = data[key].find(item => item.date === calendarDate)
        if (holiday && calendarDate === holiday.date) {
          $('.holiday').length && $('.holiday').length > 0 && $('.holiday').remove()
          $('.fc-daily-view .fc-head').append(`<td class=holiday>${holiday.name}</td>`)
        }
      }
    } catch (error) {
      localStorage.removeItem('holidays_data')
      holidayData()
    }
  }
}
export const newDaily = () => {
  let view = $('#calendar').fullCalendar('getView')
  if (view.name === 'daily') {
    $('.fc-daily-view .fc-head').empty()
    let calendar = $('#calendar')
    calendar.fullCalendar()
    calendar.hammer().off('swipeleft')
    calendar.hammer().off('swiperight')
    holidayData(() => dailyHoliday())
    dailyHoliday()
    fetchEvenes(false, () => {
      checkShowEvent()
      showPopupFromUrl()
    })
    $('.fc-time-grid-container').hammer().off('swiperight')
    $('.fc-time-grid-container').hammer().on('swiperight', () => {
      config.calendar.isRTL ? calendar.fullCalendar('next') : calendar.fullCalendar('prev')
      $('.fc-daily-view .fc-head').empty()
      holidayData(() => dailyHoliday())
      dailyHoliday()
      fetchEvenes(false, () => {
        checkShowEvent()
        showPopupFromUrl()
      })
      changeView()
    })
    $('.fc-time-grid-container').hammer().off('swipeleft')
    $('.fc-time-grid-container').hammer().on('swipeleft', () => {
      config.calendar.isRTL ? calendar.fullCalendar('prev') : calendar.fullCalendar('next')
      $('.fc-daily-view .fc-head').empty()
      holidayData(() => dailyHoliday())
      dailyHoliday()
      fetchEvenes(false, () => {
        checkShowEvent()
        showPopupFromUrl()
      })
      changeView()
    })
  }
}
