const uniqEvents = (events = []) => {
  const getUniq = (arr, full) => {
    const newObj = {}
    full
      ? arr.forEach(i => { newObj[i.id] = i })
      : arr.forEach(i => { newObj[i.id] = true })
    return newObj
  }
  const allByIds = getUniq($('#calendar').fullCalendar('clientEvents'))
  const lastEvents = events.filter(i => !allByIds[i.id])
  const lastByIds = getUniq(lastEvents, true)
  return Object.keys(lastByIds).map(i => lastByIds[i])
}
const updatedEvents = (start, end, crutch, forceFetch, cb) => {
  workers(start, end, forceFetch).then(arg => {
    if (arg.refresh) {
      $('#calendar').fullCalendar('removeEventSources')
    }
    $('#calendar').fullCalendar('addEventSource', uniqEvents(arg.events))
    window.loadedEvents = $('#calendar').fullCalendar('clientEvents')
    window.loadedEventsMap = window.loadedEvents.reduce((datesMap, i) => {
      const dateString = i.start.format('YYYY-MM-DD')
      if (!datesMap[dateString]) {
        datesMap[dateString] = []
      }
      datesMap[dateString].push(i)
      return datesMap
    }, {})
    if (crutch) {
      $('#calendar').fullCalendar('next')
      $('#calendar').fullCalendar('prev')
    }
    cb && cb()
  })
}
export const getEvents = (...arg) => {
  if (window.myEventsTimeout) {
    window.clearTimeout(window.myEventsTimeout)
    window.myEventsTimeout = null
  }
  window.myEventsTimeout = window.setTimeout(() => updatedEvents(...arg), 400)
}
export const dayOptionView = day => {
  const currentTab = $('#calendar').fullCalendar('getView').name
  if (moment().format('DD.MM.YYYY') === day && currentTab === 'agendaDay') {
    $('.fc-center').show()
    $('.fc-toolbar .fc-center h2').html('Today')
  } else {
    $('.fc-center').hide()
  }
}

export const columnHeaderText = mom => {
  const view = $('#calendar').fullCalendar('getView')
  const {daysFull} = config.translations.dates
  const {days} = config.translations.dates
  if (view.name === 'agendaDay') {
    return daysFull[moment(view.intervalStart).days()]
  }
  if (view.name === 'daily') {
    return daysFull[moment(view.intervalStart).days()]
  }
  if (view.name === 'agendaFourDay') {
    return `${days[mom.format('d')]} ${mom.format('D')}`
  }
  if (view.name === 'month') {
    return days[mom.format('d')]
  }
}

