let func
const creatingAppointment = (queryStart, obj) => {
  clearTimeout(func)
  func = setTimeout(() => {
    window.location = `${config.urls.creatingAppointmentLink}?start=${queryStart}&worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`
  }, 50)
}
export const handleDayClick = (date, jsEvent, view) => {
  let obj = qs.parse(window.location.search.slice(1))
  let queryStart = `${moment(date).format('YYYY-MM-DD')}T${moment(date).format('HH:mm:ss')}`
  if (view.name === 'daily') {
    let dailyDay = $('.fc-body')
    $(dailyDay).off('click')
    $(dailyDay).on('click', () => {
      // e.preventDefault()
      // e.stopPropagation()
      const businessHours = $('#calendar').fullCalendar('option', 'businessHours')
      businessHours.forEach(item => {
        let overlap = config.calendar.eventOverlap
        let includes = item.dow.includes(moment(date).day())
        let dateFormat = moment(date).format('YYYY-MM-DD')
        let dateNow = moment(moment(date).format('YYYY-MM-DD HH:mm')).valueOf()
        let start = moment(`${dateFormat} ${item.start}`, 'YYYY-MM-DD HH:mm').valueOf()
        let end = moment(`${dateFormat} ${item.end}`, 'YYYY-MM-DD HH:mm').valueOf()
        if (!overlap && includes && (dateNow >= start && dateNow < end)) {
          console.log('creating')
          creatingAppointment(queryStart, obj)
        } else if (overlap) {
          console.log('creating')
          creatingAppointment(queryStart, obj)
        }
      })
    })
  }
  if (view.name === 'agendaFourDay') {
    let weeklyDay = $('.fc-body')
    $(weeklyDay).off('click')
    $(weeklyDay).on('click', () => {
      // e.preventDefault()
      // e.stopPropagation()
      const businessHours = $('#calendar').fullCalendar('option', 'businessHours')
      businessHours.forEach(item => {
        let overlap = config.calendar.eventOverlap
        let includes = item.dow.includes(moment(date).day())
        let dateFormat = moment(date).format('YYYY-MM-DD')
        let dateNow = moment(moment(date).format('YYYY-MM-DD HH:mm')).valueOf()
        let start = moment(`${dateFormat} ${item.start}`, 'YYYY-MM-DD HH:mm').valueOf()
        let end = moment(`${dateFormat} ${item.end}`, 'YYYY-MM-DD HH:mm').valueOf()
        if (!overlap && includes && (dateNow >= start && dateNow < end)) {
          console.log('creating')
          creatingAppointment(queryStart, obj)
        } else if (overlap) {
          console.log('creating')
          creatingAppointment(queryStart, obj)
        }
      })
    })
  }
  if (view.name === 'month') {
    let body = $('.fc-body')[0]
    $(body).off('click')
    $(body).on('click', () => {
      $('.formonth').addClass('hidden')
      $('#calendar').fullCalendar('option', 'contentHeight', 'auto')
      $('#calendar').fullCalendar('changeView', 'daily', date)
      initEditionalFeatures()
    })
  }
}
export const getMinutes = date => date
  ? date.hours() * 60 + date.minutes()
  : '00'

export const showPopupFromUrl = () => {
  let obj = qs.parse(window.location.search.slice(1))
  const view = $('#calendar').fullCalendar('getView')
  if (obj.appointment_view === 'true') {
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        const objKey = key
        const objValue = obj[key]
        if (objKey === 'appointment_id') {
          let date = $('#calendar').fullCalendar('getDate').format('YYYY-MM-DD')
          let arrEvents = $('#calendar').fullCalendar('clientEvents')
          let event = arrEvents.find(i => i.id == objValue && moment(i.start).format('YYYY-MM-DD') === date)
          if (event) {
            history.pushState(null, null, `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}/appointments/${event.id}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`)
            eventClick(event)
            $('#calendar').fullCalendar('changeView', config.calendar.defaultView, config.calendar.defaultDate)
          }
        }
      }
    }
  }
}

export const handleViewRender = (view, element) => {
  timePanel()
  if (view.name === 'agendaDay') {
    let swiperWrapper = $(element[0].children[0].children[1])
    if (!swiperWrapper.hasClass('swiper-wrapper')) swiperWrapper.empty()
  }
}
