import { newagendacalendar } from '../agenda/agenda'
import {checkShowEvent} from 'helpers'
import { deleteEvent } from 'project-services'
import qs from 'qs'
import './deleteModal.styl'

export const renderDeleteModal = (event, id) => {
  let view = $('#calendar').fullCalendar('getView')
  // let obj = qs.parse(window.location.search.slice(1))
  const cancel = () => $('.deleteModal').remove()
  const confirm = () => {
    deleteEvent(id).then(r => {
      if (r.status === 204) {
        $('#calendar').fullCalendar('removeEvents', [id])
        if (view.name === 'agendaDay') {
          let element = $('.fc-agendaDay-view ')
          newagendacalendar(view, element)
        }
        $('.eventPopupCont').remove()
        // history.replaceState(null, null, window.location.search ? `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}` + window.location.search : `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`)
        checkShowEvent()
      }
    })
  }
  const clientName = event.name && event.name !== ''
    ? event.name
    : config.translations.occasional

  const confirmModal = $(
    `<div id='modal-background' class='deleteModal'>
      <div class='confirm-block'>
          <figure class='modal-body'>
            <img class='icon' src=${config.urls.staticImg + '/trash-del.svg'} />
            ${event.off_time
              ? `<figcaption class='text'>${config.translations.del_question_off_time.replace('{off_time_type}', config.translations.off_time_labels[event.off_time])}</figcaption>`
              : `<figcaption class='text'>${config.translations.del_question_appointment.replace('{client_name}', clientName)}</figcaption>`
            }
            
          </figure>
        <div class='modal-footer'>
          <button class='no-btn'>${config.translations.cancel.toUpperCase()}<img class='cancel-img' src=${config.urls.staticImg + '/plus-blue.svg'} /></button>
          <button class='yes-btn'>${config.translations.confirm.toUpperCase()}<img src=${config.urls.staticImg + '/confirm.svg'} /></button>
        </div>
      </div> 
    </div>`
  )
  if (!$('.deleteModal').length) {
    $('body').append(confirmModal)
  }
  $('.modal-body').on('click', e => e.stopPropagation())
  $('.no-btn').on('click', cancel)
  $('.deleteModal').on('click', cancel)
  $('.yes-btn').on('click', confirm)
}
