import {changeView, checkShowEvent, showPopupFromUrl} from 'helpers'
import {fetchEvenes, holidayData} from '../baseEvents'
import './weekly.styl'

export const renderWeeklyCalendar = () => {
  let fcAxis = $('.fc-agendaFourDay-view .fc-head .fc-axis').width()
  let fcAxisHeader = $('.fc-agendaFourDay-view .fc-head .fc-axis').outerWidth()
  let blockWidth = $('.fc-agendaFourDay-view .fc-head .fc-day-header').width()
  let headHeight = $('.fc-agendaFourDay-view .fc-head .fc-day-header').innerHeight()
  let view = $('#calendar').fullCalendar('getView')
  if (view.name === 'agendaFourDay') {
    holidayData()
    fetchEvenes(false, () => {
      if (localStorage.getItem('holidays_data')) {
        try {
          let holidaysData = JSON.parse(localStorage.getItem('holidays_data'))
          let data = holidaysData.data
          let holiday
          $('.custom-calendar-header .fc-day-header').map((i, el) => {
            for (let key in data) {
              holiday = data[key].find(item => item.date === $(el).attr('data-date'))
              if (holiday && $(el).attr('data-date') === holiday.date) {
                $(el).addClass('some-holiday').append(`<div class='weekly-holiday' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'><p>${holiday.name}</p></div>`)
              }
            }
          })
        } catch (error) {
          localStorage.removeItem('holidays_data')
          holidayData()
        }
      }
      checkShowEvent()
      showPopupFromUrl()
    }
    )
    let calendar = $('#calendar')
    calendar.fullCalendar()
    calendar.hammer().off('swipeleft')
    calendar.hammer().off('swiperight')
    $('.fc-time-grid-container').hammer().off('swiperight')
    $('.fc-time-grid-container').hammer().on('swiperight', () => {
      config.calendar.isRTL ? calendar.fullCalendar('next') : calendar.fullCalendar('prev')
      holidayData()
      fetchEvenes(false, () => {
        checkShowEvent()
        showPopupFromUrl()
      })
      changeView()
      fixedHeader(headHeight, fcAxis, blockWidth, fcAxisHeader)
    })
    $('.fc-time-grid-container').hammer().off('swipeleft')
    $('.fc-time-grid-container').hammer().on('swipeleft', () => {
      config.calendar.isRTL ? calendar.fullCalendar('prev') : calendar.fullCalendar('next')
      holidayData()
      fetchEvenes(false, () => {
        checkShowEvent()
        showPopupFromUrl()
      })
      changeView()
      fixedHeader(headHeight, fcAxis, blockWidth, fcAxisHeader)
    })
  }
  fixedHeader(headHeight, fcAxis, blockWidth, fcAxisHeader)
  changeView()
}
const fixedHeader = (headHeight, fcAxis, blockWidth, fcAxisHeader) => {
  let innerContent = $('.fc-agendaFourDay-view .fc-head .fc-head-container thead').html()
  $('.custom-calendar-header').html(innerContent)
  if (localStorage.getItem('holidays_data')) {
    try {
      let holidaysData = JSON.parse(localStorage.getItem('holidays_data'))
      let data = holidaysData.data
      let holiday
      $('.custom-calendar-header .fc-day-header').map((i, el) => {
        for (let key in data) {
          holiday = data[key].find(item => item.date === $(el).attr('data-date'))
          if (holiday && $(el).attr('data-date') === holiday.date) {
            $(el).addClass('some-holiday').append(`<div class='weekly-holiday' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'><p>${holiday.name}</p></div>`)
          }
        }
      })
    } catch (error) {
      localStorage.removeItem('holidays_data')
      holidayData()
    }
  }
  $('.custom-calendar-header .fc-axis').css(config.calendar.isRTL ? {'width': fcAxisHeader + 1, 'borderLeft': '1px solid #ededed'} : {'width': fcAxisHeader})
  $('.custom-calendar-header .fc-day-header').addClass(config.calendar.isRTL ? 'right-side' : 'left-side')
  $('.custom-calendar-header .fc-day-header').css(config.calendar.isRTL ? {width: blockWidth + 1} : {width: blockWidth + 1})
  $('.custom-calendar-header .right-side:nth-child(1)').css({'width': blockWidth + 1})
  $('.custom-calendar-header .right-side:nth-child(2)').css({'width': blockWidth + 1})
  $('.custom-calendar-header .right-side:nth-child(3)').css({'width': blockWidth + 1})
  $('.custom-calendar-header .right-side:nth-child(4)').css({'width': blockWidth})
}
