import {patchEventResize, patchEventDrop} from 'project-services'
import { showEventPopUp } from '../app/components/baseEvents'
export const checkShowEvent = () => {
  let view = $('#calendar').fullCalendar('getView')
  let calendarEvents = $('#calendar').fullCalendar('clientEvents')
  if (view.name === 'daily') {
    let start = moment(view.dateProfile.date).subtract(1, 'days').format('YYYY-MM-DD')
    let end = moment(start).add(2, 'days').format('YYYY-MM-DD')
    let currentEvents = calendarEvents.filter(i => moment(i.start).isBetween(start, end))
    showEventPopUp(currentEvents)
  } else if (view.name === 'agendaFourDay') {
    let start = moment(view.dateProfile.date).subtract(1, 'days').format('YYYY-MM-DD')
    let end = moment(start).add(5, 'days').format('YYYY-MM-DD')
    let currentEvents = calendarEvents.filter(i => moment(i.start).isBetween(start, end))
    showEventPopUp(currentEvents)
  }
}

export const eventResize = event => {
  let appointmentId = event.id
  let end = moment(event.end).format('YYYY-MM-DD HH:mm:ss')
  patchEventResize(appointmentId, end).then(checkShowEvent())
}

export const eventDrop = event => {
  let appointmentId = event.id
  let start = moment(event.start).format('YYYY-MM-DD HH:mm:ss')
  patchEventDrop(appointmentId, start).then(checkShowEvent())
}
