import { initMonthSwipe } from '../baseEvents'
import { changeView } from '../../../helpers/view.js'
import './header.styl'

export const initHeader = () => {
  let header = `
  <div id='header' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'>
    <div class='header-cont'>
      <div class='menu_refresh ${config.calendar.isRTL ? 'menu_rtl' : 'menu_ltr'}'>
        <div class='more_wrap'>
            <img src='${config.urls.staticImg}/ic_menu.svg'>
        </div>
        <div id='refresh_button'>
          <img class='refresh_button_img' src='${config.urls.staticImg}/refresh.svg'>
        </div>
      </div>
      <div class='middle-section' style='direction: ltr'>
          <div class='prev_button_wrap common'><img class='prev_button btn' src='${config.urls.staticImg}/prev.svg'></div>
          <div class='current_date'>
            <span class='today'></span>
            <span class='current_date_field'></span>
            <span class='this_week'></span>
          </div>
          <div class='next_button_wrap common'><img class='next_button btn' src='${config.urls.staticImg}/next.svg'></div>
      </div>
      <div class='header_right ${config.calendar.isRTL ? 'view_buttons_rtl' : 'view_buttons_ltr'}'>
          <button  class='today_wrap' ><img class='img_today' src='${config.urls.staticImg}/today.svg'>${config.translations.today}</button>
          <button class='daily_wrap daily_wrap_label'><img class='img_view' src='${config.urls.staticImg}/change_view.svg'>${config.translations.agenda}</button>
      </div>
    </div>
  </div>`

  $('#calendar').before(header)
  $('.current_date').on('click', () => {
    let view = $('#calendar').fullCalendar('getView')
    if (view.name !== 'month') {
      // history.pushState(null, null, `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}?worker_id=${config.activeWorkerId}&calendar_view_type=monthly`)
      $('.custom-calendar-header').addClass('hidden')
      $('.formonth').removeClass('hidden')
      $('.swiper-background .swiper-slide .fit').css({'display': 'block'})
      let firstpart = $(window).outerHeight()
      let parttwo = $('#header').outerHeight()
      let thirdPart = config.workers.length > 1 ? $('.swiper-visible').outerHeight() : 0
      let height = firstpart - (parttwo + thirdPart)
      $('#calendar').fullCalendar('option', 'contentHeight', height)
      $('#calendar').fullCalendar('changeView', 'month')
      changeView()
      let firstPart = $('#header').height()
      let secondPart = config.workers.length > 1 ? $('.swiper-visible').outerHeight() : 0
      $('#calendar').css({'marginTop': firstPart + secondPart - 1})
      // monthCalendar()
      initMonthSwipe()
      // fetchEvenes()
    }
  })
}
