import {rowCount, bday} from 'helpers'

export const customEventWeeklyOverley = (event, element, start, end, view) => {
  let container = $(element[0].children[0])
  $(container).addClass('ev-custom')
  let services = event.services
    ? event.services.reduce((phrase, item) => {
      return item.name && !!item.name ? phrase + `${item.name}` + (item.count > 1 ? ` ⨯ ${item.count},` : '') : phrase
    }, '')
    : []
  if (event.services) {
    // services = services.slice(0, -1)
    services = services.split(',')
  }
  let customEvent
  if (rowCount(event.start, event.end) >= 0.5 && rowCount(event.start, event.end) < 2) {
    customEvent = $(
      event.off_time
      ? `<div class='click-mask'></div><div class='off_time shortOff'>
          <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
        </div>`
      : `<div class='click-mask'></div><div class='custom-event'>
    <div class='name-service weeklyflex overlayOne'>
            ${event.name
    ? `<p class='client-name'>${event.name.split(' ', 1)}</p>`
    : `<p class='client-name'>${config.translations.occasional.split(' ', 1)}</p>`}
    </div>`
    )
  }
  if (rowCount(event.start, event.end) >= 2 && rowCount(event.start, event.end) < 3) {
    customEvent = $(
      event.off_time
      ? `<div class='click-mask'></div><div class='off_time twoOff'>
          <p class='event-start'>${start}</p>
          <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
        </div>`
      : `<div class='click-mask'></div><div class='custom-event'>
    <div class='name-service twoline'>
            ${event.name
    ? `<p class='client-name'>${event.name.split(' ', 1)}</p>`
    : `<p class='client-name'>${config.translations.occasional.split(' ', 1)}</p>`}
    <p class='services-name single'>${services.length > 1 ? services[0] + ' +' + (services.length - 1) : services[0]}</p>
    </div>`
    )
  }
  if (rowCount(event.start, event.end) >= 3 && rowCount(event.start, event.end) < 4) {
    customEvent = $(
      event.off_time
      ? `<div class='click-mask'></div><div class='off_time threeOff'>
        <div class='time-name'>
          <p class='event-start'>${start}</p>
          <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
        </div>
          <div class='note-address'>
        ${event.address
    ? `<div class='address-wrap'>
        <div class='img-wrap'><img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'></div>
    <p class='client-address'>${event.address}</p>
    </div>`
    : ''}
          ${event.note && !event.address
    ? `<div class='note-wrap'>
      <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
      <p class='${`client-note ${rowCount(event.start, event.end) <= 4 ? 'nowrap' : ''}`}'>${event.note}</p>
    </div>`
    : ''}
        </div>
        </div>`
      : `<div class='click-mask'></div><div class='custom-event'>
    <div class='name-service threeline overlayThree'>
            ${event.name
    ? `<p class='client-name'>${event.name.split(' ', 1)}</p>`
    : `<p class='client-name'>${config.translations.occasional.split(' ', 1)}</p>`}
    <p class='services-name single'>${services.length > 1 ? services[0] + ' +' + (services.length - 1) : services}</p>
    ${event.address
      ? `<div class='address-wrap'>
           <div class='img-wrap'><img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'></div>
      <p class='client-address'>${event.address}</p>
      </div>`
      : ''}
    ${event.note && !event.address
  ? `<div class='note-wrap'>
    <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
    <p class='${`client-note ${rowCount(event.start, event.end) <= 4 ? 'nowrap' : ''}`}'>${event.note}</p>
  </div>`
  : ''}
    </div>`
    )
  }
  if (rowCount(event.start, event.end) >= 4 && rowCount(event.start, event.end) < 5) {
    customEvent = $(
      event.off_time
      ? `<div class='click-mask'></div><div class='off_time threeOff'>
      <div class='time-name'>
        <p class='event-start'>${start}</p>
        <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
      </div>
        <div class='note-address'>
      ${event.address
  ? `<div class='address-wrap'>
        <div class='img-wrap'><img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'></div>
        <p class='client-address'>${event.address}</p>
      </div>`
  : ''}
        ${event.note
  ? `<div class='note-wrap'>
    <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
    <p class='${`client-note ${rowCount(event.start, event.end) <= 4 ? 'nowrap' : ''}`}'>${event.note}</p>
  </div>`
  : ''}
      </div>
      </div>`
      : `<div class='click-mask'></div><div class='custom-event'>
    <div class='name-service fourline overlayFour'>
            ${event.name
    ? `<p class='client-name'>${event.name.split(' ', 1)}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`
    : `<p class='client-name'>${config.translations.occasional.split(' ', 1)}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`}
    <p class='services-name single'>${services.length > 1 ? services[0] + ' +' + (services.length - 1) : services[0]}</p>
        ${event.address
    ? `<div class='address-wrap'>
        <div class='img-wrap'><img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'></div>
        <p class='client-address'>${event.address}</p>
      </div>`
    : ''}
          ${event.note
    ? `<div class='note-wrap'>
      <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
      <p class='${`client-note ${rowCount(event.start, event.end) <= 4 ? 'nowrap' : ''}`}'>${event.note}</p>
    </div>`
    : ''}
    </div>`
    )
  }
  if (rowCount(event.start, event.end) >= 5) {
    customEvent = $(
      event.off_time
      ? `<div class='click-mask'></div><div class='off_time fullOff overlayOff'>
        <p class='event-start'>${start}</p>
        <p class='name_full'>${event.off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
        <div class='note-address'>
      ${event.address
  ? `<div class='address-wrap'>
      <div class='img-wrap'><img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'></div>
      <p class='client-address'>${event.address}</p>
  </div>`
  : ''}
        ${event.note
  ? `<div class='note-wrap'>
    <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
    <p class='${`client-note ${rowCount(event.start, event.end) <= 4 ? 'nowrap' : ''}`}'>${event.note}</p>
  </div>`
  : ''}
      </div>
      </div>`
      : `<div class='click-mask'></div><div class='custom-event fullWeekly overlayFull'>
          <div class='foto-time'>
            <div class='time-info'>
            <p class='event-start'>${start} - ${end}</p>
          </div>
          <div class='name-service'>
          ${event.name
  ? `<p class='client-name'>${event.name}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`
  : `<p class='client-name'>${config.translations.occasional}${event.birthdate ? bday(event, view.dateProfile.date) : ''}${event.has_debt ? `<span class='debt'><img class='mini' src='${config.urls.staticImg}/debt1.svg'></span>` : ''}</p>`}
          <p class='services-name'>${services.map(i => `<span class='service-item'>${i}</span>`).join(' ')}</p>
          </div>
        </div>
        <div class='note-address'>
        ${event.address
    ? `<div class='address-wrap'>
        <div class='img-wrap'><img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'></div>
        <p class='client-address'>${event.address}</p>
      </div>`
    : ''}
          ${event.note
    ? `<div class='note-wrap'>
      <div class='img-wrap'><img class='feather' src='${config.urls.staticImg}/feather.svg'></div>
      <p class='${`client-note ${rowCount(event.start, event.end) <= 4 ? 'nowrap' : ''}`}'>${event.note}</p>
    </div>`
    : ''}
      </div>
    `)
  }
  $(customEvent).appendTo(container)
}
