import { getMinutes, getHoursLabel, changeView, customTimeToText, showPopupFromUrl } from 'helpers'
import { eventClick } from '../popup/popup'
// import { holidays } from 'project-services'
import { fetchEvenes, holidayData, holiday } from '../baseEvents'
import qs from 'qs'
import './agenda.styl'

const colorStr = services => {
  let nextNum = 0
  let colorStr = ''
  if (services) {
    const oneColorHeight = (100 / services.length).toFixed(2)
    for (let i = 0, l = services.length; i < l; i++) {
      const newNextNum = +nextNum + +oneColorHeight
      colorStr += ', ' + services[i].color + ' ' + nextNum + '%, ' + services[i].color + ' ' + newNextNum + '%'
      nextNum = newNextNum
    }
  }
  return colorStr
}

const renderSlide = clientEvents => {
  let element = $('.fc-agendaDay-view')
  let view = $('#calendar').fullCalendar('getView')
  for (let i = 0; i < clientEvents.length; i++) {
    let services = clientEvents[i].services
      ? clientEvents[i].services.reduce((phrase, item) => {
        return item.name && !!item.name ? phrase + `<span class='single-service'>${item.name + (item.count > 1 ? ` ⨯ ${item.count},` : '')}</span>,` : phrase
      }, '')
      : []
    if (clientEvents[i].services) {
      services = services.slice(0, -1)
      services = services.split(',')
    }
    const bday = (event, date) => {
      if (event.birthdate.length === 5) {
        if (moment(`2018-${event.birthdate}`).format('MM') === moment(date).format('MM')) {
          return `<p class='bday'><img class='bday-img' src='${config.urls.staticImg}/bday.svg'><span class='bday-text'>${config.translations.birthday}</span></p>`
        } else {
          return ''
        }
      } else if (event.birthdate.length === 10) {
        if (moment(event.birthdate).format('MM') === moment(date).format('MM')) {
          return `<p class='bday'><img class='bday-img' src='${config.urls.staticImg}/bday.svg'><span class='bday-text'>${config.translations.birthday}</span></p>`
        } else {
          return ''
        }
      }
    }
    const newClient = () => {
      return `<p class='new-cust'><img class='new-client' src='${config.urls.staticImg}/new.svg'/><span class='new-text'>${config.translations.client}</span></p>`
    }
    const debt = () => {
      return `<p class='dept'><img class='dept-img' src='${config.urls.staticImg}/debt.svg' /><span class='debt-text'>${config.translations.debt}</span></p>`
    }
    const div = $(
      `
      ${clientEvents[i].off_time
    ? `
        <div class='off_time' data-id='${clientEvents[i].id}' data-appointment_id='${clientEvents[i].id}' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'>
          <div class='off_time_wrap'>
            <img class='off_time_img' src='${clientEvents[i].off_time === 'break' ? `${config.urls.staticImg}/break.svg` : `${config.urls.staticImg}/meeting.svg`}' >
          </div>
          <div class='note-info'>
            <div class='note-about-user ${config.calendar.isRTL ? 'styleAgendaRTL' : 'styleAgendaLTR'}'>
              <p class='name-wrap'>${clientEvents[i].off_time === 'break' ? config.translations.business_lunch : config.translations.meeting}</p>
              ${clientEvents[i].note
    ? `<div class='note-wrap'>
          <img class='note-img' src='${config.urls.staticImg}/feather.svg'/>
          <p class='client-note'>${clientEvents[i].note}</p>
        </div>`
    : ''}
            ${clientEvents[i].address
    ? `<div class='address-wrap'>
          <div class='address-img-wrap'>
            <img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'>
          </div>
          <p class='address-line'>${clientEvents[i].address}</p>
        </div>`
    : ''}
        </div>
            <div class='time-block ${config.calendar.isRTL ? 'styleTimeRTL' : 'styleTimeLTR'}'>
                <div class='start-duration'>
                  <p class='event-start'>${getHoursLabel(clientEvents[i].start.hours().toString(), clientEvents[i].start.minutes().toString())}</p>
                  <div class='duration-wrap'><img class='duration-img' src='${config.urls.staticImg}/clock.svg'/><div class='event-duration'>${customTimeToText(clientEvents[i].end - clientEvents[i].start)}</div></div>
                </div>
                ${clientEvents[i].durationEditable ? `<span class='paid'>${config.translations.paid}</span>` : ''}
            </div>
          </div>
        </div>
        `
    : `
        <td class='agenda-event' data-id='${clientEvents[i].id}' data-appointment_id='${clientEvents[i].id}' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'>
        ${clientEvents[i].profile_picture && !!clientEvents[i].client_id && clientEvents[i].client_id >= 0
    ? `<div class='foto-wrap'><img class='client-foto fit' src='${config.urls.imgForClients.replace('{client_id}', clientEvents[i].client_id) + clientEvents[i].profile_picture}' onerror="this.onerror=null;this.src='${config.urls.defaultPathToClientImg}${config.defaultClientPhoto}';" /></div>`
    : !clientEvents[i].client_id || clientEvents[i].client_id <= 0
      ? `<div class='foto-wrap'><img class='client-foto fit' src='${config.urls.imgForOccasionalClients}${config.occasionalClientPhoto}' /></div>`
      : `<div class='foto-wrap'><img class='client-foto fit' src='${config.urls.defaultPathToClientImg}${config.defaultClientPhoto}' /></div>`
}
        <div class='note-info'>
          <div class='note-about-user ${config.calendar.isRTL ? 'styleAgendaRTL' : 'styleAgendaLTR'}'>
            ${clientEvents[i].name && !!clientEvents[i].name
    ? `<div class='name-wrap'><p class='client-name'>${clientEvents[i].name}</p><div class='client-icon'>${clientEvents[i].birthdate ? bday(clientEvents[i], view.dateProfile.date) : ''}${clientEvents[i].is_new_client ? newClient() : ''}${clientEvents[i].has_debt ? debt() : ''}</div></div>`
    : `<div class='name-wrap'><p class='client-name'>${config.translations.occasional}</p><div class='client-icon'>${clientEvents[i].birthdate ? bday(clientEvents[i], view.dateProfile.date) : ''}${clientEvents[i].is_new_client ? newClient() : ''}${clientEvents[i].has_debt ? debt() : ''}</div></div>`}
            <p class='services-name'>${services.join(', ')}</p>
            ${clientEvents[i].note
    ? `<div class='note-wrap'>
        <img class='note-img' src='${config.urls.staticImg}/feather.svg'/>
        <p class='client-note'>${clientEvents[i].note}</p>
      </div>`
    : ''}
          ${clientEvents[i].address
    ? `<div class='address-wrap'>
        <div class='address-img-wrap'>
          <img class='address_pin' src='${config.urls.staticImg}/grey-pin.svg'>
        </div>
        <p class='address-line'>${clientEvents[i].address}</p>
      </div>`
    : ''}
        </div>
            <div class='time-block ${config.calendar.isRTL ? 'styleTimeRTL' : 'styleTimeLTR'}' style='border-image: linear-gradient(to bottom ${colorStr(clientEvents[i].services)} ) 1 100%'>
                <div class='start-duration'>
                  <p class='event-start'>${getHoursLabel(clientEvents[i].start.hours().toString(), clientEvents[i].start.minutes().toString())}</p>
                  <div class='duration-wrap'><img class='duration-img' src='${config.urls.staticImg}/clock.svg'/><div class='event-duration'>${customTimeToText(clientEvents[i].end - clientEvents[i].start)}</div></div>
                </div>
                ${clientEvents[i].durationEditable ? `<span class='paid'>${config.translations.paid}</span>` : ''}
            </div>
        </div>
      </td>
        `
}

        `)
    $(element[0].children[0].children[1]).append(div)
    if (clientEvents[i + 1] && getMinutes(clientEvents[i].start) !== getMinutes(clientEvents[i + 1].start) && getMinutes(clientEvents[i].end) < getMinutes(clientEvents[i + 1].start)) {
      const duration = (clientEvents[i + 1].start - (
        clientEvents[i].end !== null
          ? clientEvents[i].end
          : clientEvents[i].start)) / 3600000
      let message = ''
      if (duration < 1) {
        const time = Math.floor(duration * 60)
        if (time === 1) { message = `${time} ${config.translations.duration.minute}` } else { message = `${time} ${config.translations.duration.minutes}` }
      } else {
        const time = Math.floor(duration)
        if (time === 1) { message = `${time} ${config.translations.duration.hour}` } else { message = `${time} ${config.translations.duration.hours}` }
      }
      $(element[0].children[0].children[1]).append(
        renderFreeTime(message, clientEvents[i].end)
      )
    }
  }
  if (clientEvents[0] && getMinutes(clientEvents[0].start) > getMinutes(moment(config.calendar.minTime, 'HH:mm'))) {
    const startDay = moment(clientEvents[0].start.format('YYYY-MM-DD') + config.calendar.minTime, 'YYYY-MM-DD HH:mm').format('YYYY-MM-DD HH:mm')
    const firstEvent = clientEvents[0].start.format('YYYY-MM-DD HH:mm')
    const duration = moment(firstEvent).diff(moment(startDay), 'milliseconds')
    let message = freeTime(duration)
    $('.fc-body .swiper-slide').prepend(
      renderFreeTime(message, startDay)
    )
  }
  if (clientEvents.length && clientEvents[clientEvents.length - 1] && getMinutes(clientEvents[clientEvents.length - 1].end) < getMinutes(moment(config.calendar.maxTime, 'HH:mm'))) {
    const endDay = moment(clientEvents[clientEvents.length - 1].start.format('YYYY-MM-DD') + config.calendar.maxTime, 'YYYY-MM-DD HH:mm').format('YYYY-MM-DD HH:mm')
    const lastEvent = clientEvents[clientEvents.length - 1].end.format('YYYY-MM-DD HH:mm')
    const duration = moment(endDay).diff(moment(lastEvent), 'milliseconds')
    let message = freeTime(duration)
    $(element[0].children[0].children[1]).append(
      renderFreeTime(message, lastEvent)
    )
  }
  let arrEvents = $(element[0].children[0].children[1].children).slice(1)
  return arrEvents
}
const renderFreeTime = (message, dataTime) => (
  $(`<div class='duration-time' data-time-start='${moment(dataTime).format('YYYY-MM-DD HH-mm')}'>
  <div class='duration-time-content'>
    <div class='free-time_value'>${message}</div>
    <div class='duration-time_container'><div class='free-time'><img class='cross' src='${config.urls.staticImg}/plus.svg' /></span></div></div>
    <div class='free-time_title'><span>${config.translations.duration.description}</span></div>
</div>`)
)
const freeTime = seconds => {
  let hours = Math.floor(seconds / 3600000)
  seconds -= hours * 3600000
  let minutes = Math.floor(seconds / 60000)
  if (hours === 1 && minutes === 0) {
    return `${hours} ${config.translations.duration.hour}`
  }
  if (hours === 0) {
    return `${minutes} ${config.translations.duration.minutes}`
  } else if (minutes === 0) {
    return `${hours} ${config.translations.duration.hours}`
  } else {
    return `<p class='full-dur'><span class='hours'>${hours}</span> <span>${config.translations.duration.hours}</span> <span class='minutes'>${minutes}</span> <span>${config.translations.duration.minutes}</span></p>`
  }
}
const addAppointment = () => {
  // let obj = qs.parse(window.location.search.slice(1))
  $('.duration-time').map((i, freeTimeStrip) => {
    $(freeTimeStrip).on('click', () => {
      let start = $(freeTimeStrip).attr('data-time-start')
      start = start.split(' ')
      start[1].replace('-', ':')
      start = [start[0], start[1].replace('-', ':')]
      start = start.join(' ')
      let queryStart = `${moment(start).format('YYYY-MM-DD')}T${moment(start).format('HH:mm:ss')}`
      window.location = `${config.urls.creatingAppointmentLink}?start=${queryStart}&worker_id=${config.activeWorkerId}&calendar_view_type=${config.calendar_view_type}`
      // window.location = `${config.urls.creatingAppointmentLink}?start=${queryStart}&worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`
    })
  })
}

const minHeight = () => {
  if ($('.swiper-container').is(':visible') === true) {
    $('.fc-body .swiper-slide').addClass('basic')
  } else {
    $('.fc-body .swiper-slide').addClass('full')
  }
}
const showPopUp = currentDateEvents => {
  let appointment = $('.agenda-event')
  // let obj = qs.parse(window.location.search.slice(1))
  const view = $('#calendar').fullCalendar('getView')
  appointment.map((index, appointment) => {
    $(appointment).on('click', () => {
      const id = appointment.getAttribute('data-id')
      currentDateEvents.map(event => {
        if (event.id == id) {
          // history.pushState(null, null, `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}/appointments/${event.id}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`)
          eventClick(event)
        }
      })
    })
  })
  let offTime = $('.off_time')
  offTime.map((index, offTime) => {
    $(offTime).on('click', () => {
      const id = offTime.getAttribute('data-id')
      currentDateEvents.map(event => {
        if (event.id == id) {
          // history.pushState(null, null, `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}/appointments/${event.id}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`)
          eventClick(event)
        }
      })
    })
  })
}

const renderEmptyPage = () => {
  // let obj = qs.parse(window.location.search.slice(1))
  const div = $(
    `
    <div class='empty-page' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'>
      <h1 class='agenda-title'>${config.translations.no_queues}</h1>
      <div class='agenda-text'>
        <p>${config.translations.empty_agenda}</p>
      </div>
      <div class='order-wrap'>
        <p class='order-text'>${config.translations.order_queue}</p>
        <div class='order-btn'><img class='cross' src='${config.urls.staticImg}/plus.svg' /></span></div>
      </div>
    </div>
    `)
  $('.swiper-agenda-container .swiper-wrapper .swiper-slide').append(div)
  $('.order-wrap').on('click', () => {
    let start = $('#calendar').fullCalendar('getDate').format('YYYY-MM-DD')
    window.location = `${config.urls.creatingAppointmentLink}?worker_id=${config.activeWorkerId}&start=${start}&calendar_view_type=${config.calendar_view_type}`
    // window.location = `${config.urls.creatingAppointmentLink}?worker_id=${config.activeWorkerId}&start=${start}&calendar_view_type=${obj.calendar_view_type}`
  })
  $('.empty-page').css({'backgroundImage': 'url(' + config.urls.staticImg + '/empty-agenda.svg'})
}
const agendaHoliday = () => {
  if (localStorage.getItem('holidays_data') && !$('.holiday').length) {
    try {
      let holidaysData = JSON.parse(localStorage.getItem('holidays_data'))
      let data = holidaysData.data
      let calendarDate = $('#calendar').fullCalendar('getDate').format('YYYY-MM-DD')
      let holiday
      for (let key in data) {
        holiday = data[key].find(item => item.date === calendarDate)
        if (holiday && calendarDate === holiday.date) {
          $('.holiday').length && $('.holiday').length > 0 && $('.holiday').remove()
          $('.fc-body .swiper-slide').prepend(`<td class="holiday">${holiday.name}</td>`)
        }
      }
    } catch (error) {
      localStorage.removeItem('holidays_data')
      holidayData(() => agendaHoliday())
    }
  }
}

export const newagendacalendar = () => {
  $('.fc-head').empty()
  if ($('.swiper-container').is(':visible') === true) {
    $('.fc-agendaDay-view').addClass('basic')
  } else {
    $('.fc-agendaDay-view').addClass('full')
  }
  let calendar = $('#calendar')
  calendar.fullCalendar()
  let swiperWrap = $('.fc-body')
  holidayData(() => agendaHoliday())
  fetchEvenes(false, () => {
    let swiperCont = $('.fc-agendaDay-view')
    swiperCont.children().first().addClass('swiper-agenda-container')
    swiperWrap.empty().addClass('swiper-wrapper')
    let currentDate = $('#calendar').fullCalendar('getDate')
    let calendarEvents = $('#calendar').fullCalendar('clientEvents')
    let currentDateEvents = calendarEvents.filter(i => moment(i.start).isSame(currentDate, 'day')).sort((a, b) => {
      return moment(a.start._i) - moment(b.start._i)
    })
    swiperWrap.append('<tr class="swiper-slide"></tr>').children().append(currentDateEvents.length > 0 ? renderSlide(currentDateEvents) : renderEmptyPage())
    agendaHoliday()
    showPopUp(currentDateEvents)
    addAppointment()
    showPopupFromUrl()
    minHeight()
  })
  calendar.hammer().off('swipeleft')
  calendar.hammer().on('swipeleft', () => {
    config.calendar.isRTL ? calendar.fullCalendar('prev') : calendar.fullCalendar('next')
    // calendar.fullCalendar('next')
    holidayData(() => agendaHoliday())
    fetchEvenes()
    showPopupFromUrl()
    let currentDate = $('#calendar').fullCalendar('getDate')
    let calendarEvents = $('#calendar').fullCalendar('clientEvents')
    let currentDateEvents = calendarEvents.filter(i => moment(i.start).isSame(currentDate, 'day')).sort((a, b) => {
      return moment(a.start._i) - moment(b.start._i)
    })
    swiperWrap.empty()
    swiperWrap.append('<tr class="swiper-slide"></tr>').children().append(currentDateEvents.length > 0 ? renderSlide(currentDateEvents) : renderEmptyPage())
    agendaHoliday()
    showPopUp(currentDateEvents)
    addAppointment()
    minHeight()
    changeView()
  })
  calendar.hammer().off('swiperight')
  calendar.hammer().on('swiperight', () => {
    config.calendar.isRTL ? calendar.fullCalendar('next') : calendar.fullCalendar('prev')
    holidayData(() => agendaHoliday())
    fetchEvenes()
    showPopupFromUrl()
    let currentDate = $('#calendar').fullCalendar('getDate')
    let calendarEvents = $('#calendar').fullCalendar('clientEvents')
    let currentDateEvents = calendarEvents.filter(i => moment(i.start).isSame(currentDate, 'day')).sort((a, b) => {
      return moment(a.start._i) - moment(b.start._i)
    })
    swiperWrap.empty()
    swiperWrap.append('<tr class="swiper-slide"></td>').children().append(currentDateEvents.length > 0 ? renderSlide(currentDateEvents) : renderEmptyPage())
    agendaHoliday()
    showPopUp(currentDateEvents)
    addAppointment()
    minHeight()
    changeView()
  })
  // showPopUp(currentDateEvents)
  // minHeight()
}
