import './swiper.styl'

export default () => {
  // Init first worker
  const workers = config.workers.map(worker => {
    let active = ''
    if (config.activeWorkerId == worker.id) {
      active = ' active'
    }
    return `
      <figure data-index='${worker.id}' class='swiper-slide${active}'>
         <img class='fit' src='${config.urls.imgForWorkers.replace('{worker_id}', worker.photo)}' onerror="this.onerror=null;this.src='${config.urls.defaultPathToWorkerImg}${config.defaultWorkerPhoto}';" />
         <figcaption class='swiper-title'>${worker.name}</figcaption>
      </figure>`
  })
  return `
    <div class='swiper-container' style='${config.calendar.isRTL ? 'direction: rtl' : 'direction: ltr'}'>
      <div class='swiper-wrapper'>
        ${workers.join('')}
      </div>
      <div class='swiper-button-next'></div>
      <div class='swiper-button-prev'></div>
    </div>
  `
}
